.clearfix {
  *zoom: 1;
  &:before {
    display: table;

    content: '';
  }
  &:after {
    display: table;
    clear: both;

    content: '';
  }
}
.clear {
  clear: both;
}
#sbox-overlay {
  position: absolute;
  top: 0;
  left: 0;

  background-color: #000;
}

#sbox-window {
  position: absolute;

  overflow: visible;

  padding: 10px;

  text-align: left;

  border-radius: 3px;
  background-color: #fff;
  &[aria-hidden=true] {
    display: none;
  }
}

#sbox-overlay[aria-hidden=true] {
  display: none;
}

#sbox-btn-close {
  position: absolute;
  top: -15px;
  right: -15px;

  width: 30px;
  height: 30px;

  border: none;
  background: url(../img/closebox.png) no-repeat center;
}

.sbox-loading #sbox-content {
  background-image: url(../img/spinner.gif);
  background-repeat: no-repeat;
  background-position: center;
}

#sbox-content {
  clear: both;
  overflow: auto;

  width: 100%;
  height: 100%;

  background-color: #fff;
}

.sbox-content-image#sbox-content {
  overflow: visible;
}

#sbox-image {
  display: block;
}

.sbox-content-image img {
  display: block;

  width: 100%;
  height: 100%;
}

.sbox-content-iframe#sbox-content {
  overflow: visible;
}

/* Hides scrollbars */

.body-overlayed {
  overflow: hidden;
  embed,
  object,
  select {
    visibility: hidden;
  }
}

/* Hides flash (Firefox problem) and selects (IE) */

#sbox-window {
  embed,
  object,
  select {
    visibility: visible;
  }
  &.shadow {
    box-shadow: 0 0 10px rgba(0, 0, 0, .7);
  }
}

/* Shadows */

.sbox-bg {
  position: absolute;

  width: 33px;
  height: 40px;
}

.sbox-bg-n {
  top: -40px;
  left: 0;

  width: 100%;

  background: url(../img/bg_n.png) repeat-x;
}

.sbox-bg-ne {
  top: -40px;
  right: -33px;

  background: url(../img/bg_ne.png) no-repeat;
}

.sbox-bg-e {
  top: 0;
  right: -33px;

  height: 100%;

  background: url(../img/bg_e.png) repeat-y;
}

.sbox-bg-se {
  right: -33px;
  bottom: -40px;

  background: url(../img/bg_e.png) no-repeat;
}

.sbox-bg-s {
  bottom: -40px;
  left: 0;

  width: 100%;

  background: url(../img/bg_s.png) repeat-x;
}

.sbox-bg-sw {
  bottom: -40px;
  left: -33px;

  background: url(../img/bg_sw.png) no-repeat;
}

.sbox-bg-w {
  top: 0;
  left: -33px;

  height: 100%;

  background: url(../img/bg_w.png) repeat-y;
}

.sbox-bg-nw {
  top: -40px;
  left: -33px;

  background: url(../img/bg_nw.png) no-repeat;
}

@-moz-document url-prefix() {
  .body-overlayed {
    overflow: visible;
  }
}

